'use client';

import { useState, useEffect } from 'react';
import styles from './LoginGate.module.scss';

export default function LoginGate({ children }: { children: React.ReactNode }) {
    const [isAuthenticated, setIsAuthenticated] = useState(false);
    const [password, setPassword] = useState('');
    const [error, setError] = useState(false);

    useEffect(() => {
        const sessionAuth = sessionStorage.getItem('is_authenticated');
        if (sessionAuth === 'true') {
            setIsAuthenticated(true);
        }
    }, []);

    const handleLogin = (e: React.FormEvent) => {
        e.preventDefault();
        if (password === 'louis_bewerbung') {
            setIsAuthenticated(true);
            sessionStorage.setItem('is_authenticated', 'true');
            setError(false);
        } else {
            setError(true);
            setPassword('');
        }
    };

    if (isAuthenticated) {
        return <>{children}</>;
    }

    return (
        <div className={styles.loginContainer}>
            <div className={styles.loginCard}>
                <h1>Willkommen</h1>
                <p>Bitte geben Sie das Zugangspasswort ein.</p>

                <form onSubmit={handleLogin} className={styles.form}>
                    <input
                        type="password"
                        placeholder="Passwort"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        className={error ? styles.inputError : ''}
                    />
                    {error && <span className={styles.errorMessage}>Falsches Passwort</span>}
                    <button type="submit">Eintreten</button>
                </form>
            </div>
        </div>
    );
}
